<template>
  <div class="layout">
    <Row :gutter="16">
      <Col span="12">
      <Card>
        <p slot="title">我的任务</p>
        <router-link to="/work" slot="extra">
          更多
          <Icon type="ios-arrow-right"></Icon>
        </router-link>
        <Table :row-class-name="rowClassName" border :columns="columns" :data="datas"></Table>
      </Card>
      </Col>
      <Col span="12">
      <Card>
        <p slot="title">我的Bug</p>
        <router-link to="/work" slot="extra">
          更多
          <Icon type="ios-arrow-right"></Icon>
        </router-link>
        <Table border :columns="columns" :data="datas"></Table>
      </Card>
      </Col>
    </Row>
    <br/>
    <Row :gutter="16">
      <Col span="12">
      <Card>
        <p slot="title">我的文档</p>
        <router-link to="/work" slot="extra">
          更多
          <Icon type="ios-arrow-right"></Icon>
        </router-link>
        <Table border :columns="columns" :data="datas"></Table>
      </Card>
      </Col>
      <Col span="12">
      <Card>
        <p slot="title">我的周报</p>
        <router-link to="/work" slot="extra">
          更多
          <Icon type="ios-arrow-right"></Icon>
        </router-link>
        <Table border :columns="columns" :data="datas"></Table>
      </Card>
      </Col>
    </Row>
    <div class="layout-copy">
      2011-2017 &copy; Chayu OA
    </div>
  </div>
</template>

<script>
  import topnav from 'components/topnav/topnav'

  export default {
    components: {
      'topnav': topnav
    },
    data () {
      return {
        columns: [
          {
            title: '标题',
            key: 'tit',
            render (row) {
              return `<a href="#">${row.tit}</a>`
            }
          },
          {
            title: '优先级',
            key: 'up',
            align: 'center',
            width: 80
          },
          {
            title: '计划完成时间',
            key: 'time',
            align: 'center',
            width: 120
          }
        ],
        datas: [
          {
            'num': 'RW0001',
            'up': '4',
            'tit': '任务内容详细',
            'time': '2017.3.3'
          },
          {
            'num': 'RW0001',
            'up': '4',
            'tit': '任务内容详细任务内容详细任务内容详细任务内容详细任务内容详细任务内容详细',
            'time': '2017.3.3'
          },
          {
            'num': 'RW0001',
            'up': '4',
            'tit': '任务内容详细',
            'time': '2017.3.3'
          },
          {
            'num': 'RW0001',
            'up': '4',
            'tit': '任务内容详细',
            'time': '2017.3.3'
          },
          {
            'num': 'RW0001',
            'up': '4',
            'tit': '任务内容详细',
            'time': '2017.3.3'
          }
        ]
      }
    },
    methods: {
      rowClassName (row, index) {
        if (index === 0) {
          return 'a'
        } else if (index === 1) {
          return 'b'
        } else if (index === 2) {
          return 'c'
        }
        return ''
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .layout
    padding: 20px
  .layout-copy
    text-align: center
    padding: 20px 0 10px
    color: #9ea7b4

</style>
<style lang="stylus" rel="stylesheet/stylus">
  .ivu-table .c td
    background-color: #fff5f5
  .ivu-table .b td
    background-color: #ffeeee
  .ivu-table .a td
    background-color: #ffe5e5
</style>



